.container {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  max-width: 1440px;
  padding: 16px;
}
@media screen and (min-width: 1000px) {
  .container {
    flex-direction: row;
    justify-content: space-around;
    padding: 32px 0;
  }
}
.container__image {
  height: 330px;
  max-width: 505px;
  width: 100%;
}
.container__image__picture {
  height: 100%;
  width: 100%;
}
.container__content {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-top: 24px;
  max-width: 380px;
}
.container__content__paragraph {
  margin-top: 24px;
  text-align: justify;
}
.container__content__button {
  align-self: flex-start;
  background: #e4c1ae;
  color: #ffffff;
  border-radius: 32px;
  font-size: 16px;
  font-weight: 700;
  margin-top: 32px;
  padding: 12px 36px;
}
.container__content__button:hover {
  background: #d7a488;
}
@media screen and (min-width: 1000px) {
  .container__content__button {
    padding: 16px 40px;
  }
}

.popup {
  background: rgba(18, 18, 18, 0.5019607843);
  display: flex;
  height: 100vh;
  position: absolute;
  width: 100vw;
}
.popup__window {
  background: #ffffff;
  margin: auto;
  max-width: 920px;
  padding: 56px 95px;
  position: relative;
  width: 90vw;
}
.popup__window__content {
  margin-top: 24px;
}
.popup__window__close {
  cursor: pointer;
  height: 12px;
  position: absolute;
  right: 46px;
  top: 46px;
  width: 12px;
}
.popup__window__close__left, .popup__window__close__right {
  background: #000000;
  display: block;
  height: 2px;
  position: absolute;
  left: 0;
  width: 13.5px;
}
.popup__window__close__left {
  top: 0;
  transform: rotate(45deg);
  transform-origin: 0 100%;
}
.popup__window__close__right {
  bottom: 0;
  transform: rotate(-45deg);
  transform-origin: 0 0;
}

html {
  color: #121212;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 42px;
  font-weight: 700;
  line-height: 1.2em;
}

p {
  font-weight: 300;
  line-height: 1.5em;
}

button {
  border: none;
  cursor: pointer;
  outline: none;
}